@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100..900&display=swap');

.message {
  width: 842px;
  font-family: Montserrat;
  display: inline-block;
  padding: 20px 0px;
  margin-left: 390px;
  margin-right: 70px;
}

.imageAndText {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  gap: 10px;

}

.imageAndText img {
  width: 45px;
}

.text {
  max-width: auto;
  background: #0066CC;
  box-shadow: 0px 1px 2px 0px #1D21261A, 0px 5px 20px 0px #1D212608;
  font-size: 16px;
  font-weight: 400;
  line-height: 19.5px;
  text-align: right;
  padding: 10px;
  color: #FFFFFF;
  word-wrap: break-word;
  border-radius: 10px 10px 10px 10px;
}


.messageText {
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
  text-align: right;
}

.name {
  font-size: 18px;
  font-weight: 600;
  line-height: 22px;
  text-align: right;

}

.time {
  font-size: 10px;
  font-weight: 600;
  color: #000000;
  margin-left: 802px;
  padding: 0 8px;
}